	import ajax from '../../utils/ajax.js';
	import {mapMutations} from 'vuex';
 	export default {
		data() {
			return {
				good:{}
			}
		},
		onLoad(options){
			
		},
		created(options){
			// console.log('created',options)
			// console.log('created',this)
			// 通过this身上的$mp属性可以获取到query参数
			// console.log('goodId',this.$mp)
			// let {goodId} = this.$mp.query;
		},
		async mounted(){
			// created中无法获取到query参数,必须在mounted之后才能获取
			console.log('goodId',this.$mp)
			let {goodId} = this.$mp.query;
			let result =await ajax('/getGoodDetail',{goodId});
			console.log('good',result)
			this.good=result;
		},
		methods:{
			...mapMutations(["addShopItem"]),
			add(){
				uni.showToast({
					title:"添加购物车成功"
				})
				this.addShopItem(this.good);
			}
		}
	}
